<template>
  <g-body-layout
    :pageIndex="pageNumber"
    :pageSize="pageSize"
    :total="total"
    :filterText.sync="filterText"
    @pageIndexChange="pageIndexChange"
    @pageSizeChange="pageSizeChange"
    @suffixSearchCallback="onFilterReady"
  >
    <!-- 按钮组插槽 -->
    <template #btnGroup>
      <g-btn-group :buttonLists="btnList" @btnCallBack="btnGroupClick">
      </g-btn-group>
    </template>
    <!-- g-ndo-table插槽 -->
    <template #table>
      <g-ndo-table
        :id="id"
        :handName="'设备'"
        :dataList="dataList"
        :isTableLoading="loading"
        :total="total"
        @rowClick="rowClick"
      ></g-ndo-table>
    </template>
    <!-- 详细内容插槽 -->
    <template #content>
      <create-or-edit-resource
        :ndoEditParams="ndoEditParams"
        :refreshTag="refreshTag"
        :saveTag="saveTag"
        :resetTag="resetTag"
        @childRefresh="childRefresh($event)"
        @rowClick="rowClick($event)"
        v-if="isShowEdit"
      ></create-or-edit-resource>
      <g-empty v-else> </g-empty>
    </template>
  </g-body-layout>
</template>

<script>
import CreateOrEditResource from "./create-or-edit-resource/create-or-edit-resource.vue";
import GCopyNdo from "@/components/g-copy-ndo/g-copy-ndo.vue";

import { GNdoPageBase } from "@/utils";

export default {
  name: "DataV2Resource",
  mixins: [GNdoPageBase],
  components: {
    CreateOrEditResource,
  },

  data() {
    return {
      isTableLoading: false,
      filterText: "",
    };
  },

  mounted() {},
  created() {
    this.getBtnList();
  },
  methods: {
    getBtnList() {
      // 获取按钮列表
      this.btnList = [
        {
          text: this.AppConst.button.create,
          icon: this.AppConst.icon.create,
          eventName: this.AppConst.button.create,
          display: true,
        },
        {
          text: this.AppConst.button.copy,
          icon: this.AppConst.icon.copy,
          eventName: this.AppConst.button.copy,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.delete,
          icon: this.AppConst.icon.delete,
          eventName: this.AppConst.button.delete,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.refresh,
          icon: this.AppConst.icon.refresh,
          eventName: this.AppConst.button.refresh,
          right: true,
          display: this.isShowEdit,
        },
        {
          text: this.AppConst.button.save,
          icon: this.AppConst.icon.save,
          eventName: this.AppConst.button.save,
          right: true,
          type: "primary",
          display: this.isShowEdit,
        },
      ];
    },
    copy() {
      this.createModal(
        { title: "复制", width: "600px" },
        {
          component: GCopyNdo,
          props: {
            entity: {
              copyValue: this.dataList[0].name,
              value: this.dataList[0].name,
            },
          },
          on: {
            success: (res) => {
              console.log(res);
              // 手动延时
              this.loading = true;
              setTimeout(() => {
                this.loading = false;
                this.copySuccess(res.value, res.copyValue);
                this.refreshGoFirstPage();
              }, 500);
            },
          },
        }
      );
    },
    delete() {
      // 删除确认
      this.$confirm({
        title: "确认删除",
        content: "确认删除该设备吗？删除后无法恢复。",
        onOk: () => {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            // 消息提示
            this.deleteSuccess();
            // 刷新
            this.refresh();
          }, 500);
        },
      });
    },
    fetchData(request) {
      this.loading = true;
      setTimeout(() => {
        request.finishedCallback();
        // 填充dataList 示例数据
        let dataList = [];
        const familyNames = ["交换机", "路由器", "服务器", "存储设备", "防火墙"];
        const groupNames = [
          "核心交换组", "接入交换组", "边界路由组", "核心路由组", 
          "应用服务器组", "数据库服务器组", "主存储组", "备份存储组", 
          "边界防火墙组", "内网防火墙组"
        ];
        const statuses = ["在线", "离线", "维护中", "故障"];
        const manufacturers = ["华为", "思科", "惠普", "戴尔", "IBM", "联想"];
        
        for (let i = 0; i < 25; i++) {
          const familyIndex = i % 5;
          const groupIndex = (familyIndex * 2 + i % 2);
          
          dataList[i] = {
            id: i + 1,
            name: familyNames[familyIndex] + (i * this.pageNumber + 1).toString().padStart(2, '0'),
            familyName: familyNames[familyIndex],
            familyId: familyIndex + 1,
            groupName: groupNames[groupIndex],
            groupId: groupIndex + 1,
            ipAddress: `192.168.${familyIndex + 1}.${i + 1}`,
            model: `Model-${familyNames[familyIndex]}-${i % 3 + 1}`,
            serialNumber: `SN${(i + 1).toString().padStart(5, '0')}`,
            status: statuses[i % 4],
            manufacturer: manufacturers[i % 6],
            description: `${familyNames[familyIndex]}设备，用于${groupNames[groupIndex]}`,
            createdBy: "admin",
            createdTime: "2025-03-18 " + (10 + i % 10) + ":" + (i % 60),
          };
        }

        // filter 过滤
        if (this.filterText) {
          console.log(this.filterText);
          dataList = dataList.filter((item) => {
            return item.name.indexOf(this.filterText) > -1 || 
                   item.familyName.indexOf(this.filterText) > -1 ||
                   item.groupName.indexOf(this.filterText) > -1 ||
                   item.ipAddress.indexOf(this.filterText) > -1 ||
                   item.model.indexOf(this.filterText) > -1 ||
                   item.serialNumber.indexOf(this.filterText) > -1 ||
                   item.status.indexOf(this.filterText) > -1 ||
                   item.manufacturer.indexOf(this.filterText) > -1 ||
                   item.description.indexOf(this.filterText) > -1;
          });
        }

        request.successCallback({
          items: dataList,
          totalCount: dataList.length * 2,
        });
      }, 1000);
    },
  },
};
</script>

<style lang="scss" scoped></style>